
import { db } from "@/main";
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonItem,
  IonAvatar,
  IonList,
  IonIcon,
  IonLabel,
  actionSheetController,
} from "@ionic/vue";
import { chevronForwardOutline } from "ionicons/icons";
import { defineComponent } from "vue";
export default defineComponent({
  name: "manejoUsuarios",
  components: {
    IonHeader,
    IonToolbar,
    IonTitle,
    IonContent,
    IonPage,
    IonItem,
    IonAvatar,
    IonList,
    IonIcon,
    IonLabel,
  },
  data() {
    return {
      usuarios: [],
    };
  },
  setup() {
    async function mostrarOpcionesUsuario(nombre: string, id: string) {
        const actionSheet = await actionSheetController
        .create({
          header: nombre,
          buttons: [
            {
              text: 'Eliminar cuenta',
              role: 'destructive',
              handler: () => {
                console.log('Eliminar clickado')
              },
            },
            {
              text: 'Cancelar',
              role: 'cancel',
              handler: () => {
                console.log('Cancelar clickado')
              },
            },
          ],
        });
      await actionSheet.present();

      const { role } = await actionSheet.onDidDismiss();
      if(role === "destructive") {
        await db.collection("users").doc(id).delete();
        this.usuarios = [];
        this.obtenerListadoUsuarios();
      }
    }
    async function obtenerListadoUsuarios() {
      await db
        .collection("users")
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.usuarios.push({
              id: doc.id,
              nombreUsuario: doc.data().nombre + doc.data().apellidos,
              fotoUsuario: doc.data().fotoPerfil,
              email: doc.data().email,
            });
          });
        });
    }
    return {
      chevronForwardOutline,
      obtenerListadoUsuarios,
      mostrarOpcionesUsuario,
    };
  },
  mounted() {
    this.obtenerListadoUsuarios();
  },
});
